.header
{
  position: sticky;
  .coedlib{
    text-align: center;
    margin-bottom: 16px;
    color: rgb(47, 189, 189);
  }
}
.main-theme{
  position: relative;
  background: gray;
.theme{
  position: absolute;
  right: 0;

  .img{
    padding: 10px;
    transition: transform 0.4s ease;
  }
  .img:hover{
    cursor: pointer;
    transform: scale(1.4);
  }
}
}
.search-container{
  .form
  {
    margin-top: 10px;
    padding:2px;
    text-align: center;
    #search-input{
      padding: 8px;
      border: 1px solid #ccc;
      border-radius: 4px;
      margin-right: 8px;
      font-size: 16px;
    }
    #search-input:focus {
      outline: none;
      border-color: #007bff;
    }
    #search-input::placeholder {
      color: #999;
    }
    #search-button {
      padding: 8px 16px;
      background-color: #007bff;
      color: #fff;
      border: none;
      border-radius: 4px;
      font-size: 16px;
      cursor: pointer;
    }
    #search-button:hover {
      background-color: #0056b3;
    }
  }
}
.header-part{
 position: sticky;
 top: 0;
 background: rgb(37, 37, 37);
 top: 0;
}
.book-card {
  display:inline-block;
  text-align: center;
  padding: 40px;
  .book-info{
    padding: 10px;
    .book-info1{
      padding: 5px;
    }
  }
  #books-ul{
    list-style-type:inherit;
  }
}
.home{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.3em;
  height: auto;
  display: block;
  text-align: center;
  padding: 10px;
  word-spacing: 10px;
}
.menu{
  .menu-ul{
    position: sticky;
    top: 0;
    list-style-type:  none;
    display: inline-flex;
    #li{
      padding: 5px;
      color: #bfcddd;
    }
  }
}
.book-img{
  transition: transform 0.5s  ease;
}
.book-img:hover{
 transform: scale (1.2);
}
.dark{
  background: rgb(44, 43, 43);
  color: aqua;
  width: 100%;
  height: 100%;
  }
.light{
    background: #fff;
    width: 100%;
    height: 100%;
}
#main{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
/* Hide the scroll bar */
::-webkit-scrollbar {
  display: none;
}
.loader{
  color: blue;
}
